import React from "react";
import {
  Card,
  CardImg,
  CardImgOverlay,
  CardTitle,
  Breadcrumb,
  BreadcrumbItem
} from "reactstrap";
// import DishDetail from "./DishdetailComponent";
import {Link} from "react-router-dom";
import {Loading} from "./LoadingComponent";
import {baseUrl} from "../shared/baseUrl";


function RenDerMenuItem({dish, toClick}) {
  return(
/* CPN cha (Main CPN)  truyn toClick vo vo props  c th s dng toClick phi c props v sau khi click s truyn dish.id qua CPN cha, sau  CPN cha s gi hm onDishSelect(dishId)  thay i selectedDish: dish.id  state */
      <Card>
        <Link to={`/menu/${dish.id}`}>
          <CardImg width="100%" src={baseUrl + dish.image} alt={dish.name} />
          <CardImgOverlay>
              <CardTitle>{dish.name}</CardTitle>
          </CardImgOverlay>
        </Link>
      </Card>
  );
}

const Menu = (props)=>{
      // v MenuComponents khng phi l Component cha nn mun s dng dishes c thit lp  CPN cha l main CPN nn phi s dng props
      const menu = props.dishes.dishes.map((dish) => {
        return (
          <div key={dish.id} className="col-12 col-md-5 m-1">
            <RenDerMenuItem dish={dish} toClick={props.toClick} />
          </div>
        
        );
      });
  
      if(props.dishes.isLoading){
        return(
          <div className="container">
            <div className="row">
              <Loading />
            </div>
          </div>
        );
      }else if(props.dishes.errMess){
        return(
          <div className="container">
            <div className="row">
              <h4>{props.dishes.errMess}</h4>
            </div>
          </div>
        );
      }else
        return (
          <div className="container">
            <div className="row">
              <Breadcrumb>
                <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                <BreadcrumbItem active>Menu</BreadcrumbItem>
              </Breadcrumb>
            
              <div className="col-12">
                <h3>Menu</h3>
                <hr />
              </div>
            </div>
            <div className="row">
              {menu}
            </div>
          </div>
        );
}

  // renderDish(dish) {
  //   if (dish != null) {
  //     return (
  //       <Card>
  //         <CardImg width="100%" src={dish.image} alt={dish.name} />
  //         <CardBody>
  //           <CardTitle>{dish.name}</CardTitle>
  //           <CardText>{dish.description}</CardText>
  //         </CardBody>
  //       </Card>
  //     );
  //   } else {
  //     return <div></div>;
  //   }
  // }





export default Menu;



